import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
class Logo extends Component {
  

    componentDidMount() {
        window.ethereum.enable()
    }

    async my_avatar(){
        window.ethereum.enable().then(async function (accounts) {
        window.history.pushState('avatar_page', 'AVATAR PAGE', '/smartshop/?account='+accounts[0])
      })
        this.props.update("EmptyAvatar")
      }

 async jump(){
    window.ethereum.enable().then(async function (accounts) {
        
        window.history.pushState('account_page', 'ACCOUNT PAGE', '/happyfarm/?account='+accounts[0]) 
    })
    this.props.update("Empty")
 }

    render(){
        return (
        <div id="lips" opacity="1" >
            <div id="smartshop_logo"  alt="smartshop"   onClick={() => this.props.update("SmartShop")}></div><div id="lips_label" className="text white"> SmartShop</div><div id="menu_button" className="light lightblue text black">MENU</div>
            <div id="lips_menu" className="dark darkblue">
                <div className="menu_row">
                    <div className="lips_menu_link lhovermenu lighthover lips_dir_button float_left" onClick={() => this.props.update("Book")}>BOOK<br></br><img className="lips_dir_icon"  alt="Book" src="https://i.imgur.com/Bq0jqhs.png"/></div>
                    <div className="lips_menu_link lhovermenu lighthover  lips_dir_button float_left"     onClick={() => window.open("https://dapp.dfohub.com/?addr=0x62fa91C62f34863cfb57c583623D48D77f30656B")}>DFO<br></br><img className="lips_dir_icon"  alt="dfo" src="https://i.imgur.com/YMxB90E.png"/></div>
                    <div className="lips_menu_link lhovermenu lighthover  lips_dir_button float_left"    onClick={() => this.my_avatar()}>ACCOUNT<br></br><img className="lips_dir_icon"  alt="account" src="https://i.imgur.com/YdWi6EE.png"/></div>
                    <div className="lips_menu_link lhovermenu lighthover  lips_dir_button float_left" onClick={() => this.props.update("EmptyShop")}>SHOP<br></br><img className="lips_dir_icon"  alt="Shop" src="https://i.imgur.com/A39y87l.png"/></div>
                    <div className="lips_menu_link lhovermenu lighthover  lips_dir_button float_left" onClick={() => this.props.update("Favorites")}>FAVES<br></br><img className="lips_dir_icon"  alt="Favorites" src="https://i.imgur.com/tOerThN.png"/></div>
                           
                    <div><a className="lips_menu_link lhovermenu lighthover  lips_dir_button float_right text white" href="https://github.com/HappyFarm-DFO/smartshop/" target="_blank" rel="noopener noreferrer">GITHUB<br></br><img className="lips_dir_icon" alt="github" src="https://i.imgur.com/Roc9b0U.png"/></a></div>
                    <div><a className="lips_menu_link lhovermenu lighthover  lips_dir_button float_right text white" href="https://discord.gg/F3Cqksx" target="_blank" rel="noopener noreferrer">DISCORD<br></br><img alt="discord" className="lips_dir_icon" src="https://i.imgur.com/tZbrqGS.png"/></a></div>
                </div>
                <div className="menu_row">
                    <div className="lips_menu_link lhovermenu lighthover  lips_dir_button float_left" href="https://uniswap.info/token/0x801f90f81786dc72b4b9d51ab613fbe99e5e4ccd"   onClick={() => this.props.update("Bows")}>TOKEN<br></br><img className="lips_dir_icon"  alt="token" src="https://i.imgur.com/wlum6zh.png"/></div>                
                    <div className="lips_menu_link lhovermenu lighthover lips_dir_button float_left" onClick={() => this.props.update("Manifest")}>MANIFEST<br></br><img className="lips_dir_icon"  alt="MANIFEST" src="https://i.imgur.com/RE78h1z.png"/></div>
                    <div className="lips_menu_link lhovermenu lighthover  lips_dir_button float_left"     onClick={() => this.props.update("Opportunities")}>PROPS<br></br><img className="lips_dir_icon"  alt="OPPORTUNITIES" src="https://i.imgur.com/TwGkFfv.png"/></div>
                    <div className="lips_menu_link lhovermenu lighthover  lips_dir_button float_left"    onClick={() => this.props.update("Referral")}>REFERZ<br></br><img className="lips_dir_icon"  alt="REFERRAL ECONOMY" src="https://i.imgur.com/AItgyOV.png"/></div>
                    <div className="lips_menu_link lhovermenu lighthover  lips_dir_button float_left" onClick={() => this.props.update("Dev")}>DEVS<br></br><img className="lips_dir_icon"  alt="DEVELOPMENT" src="https://i.imgur.com/6PWYs90.png"/></div>
        
                    <div><a className="lips_menu_link lhovermenu lighthover  lips_dir_button float_right text white" onClick={() => this.props.update("Roadmap")}>ROADMAP<br></br><img className="lips_dir_icon" alt="github" src="https://i.imgur.com/WJpyYGg.png"/></a></div>  
                </div>
            </div>
        </div>
        );
    }
}
export default Logo