import React, { Component } from 'react'
import './css.css';
import Web3 from 'web3';
import utils from './../../library'

class Book extends Component {
constructor(props){
    super()
    this.openShop = this.openShop.bind(this);
    this.state={page:0,backside:false}
}
    componentDidMount(){
        this.loadShops(0)
        utils.loadOwnerColors()
    }

    async openShop(shop){
        window.history.pushState('show_shop', 'Shop Display', '/smartshop/?shop='+shop);
        this.props.jumptoshop()
    }

    async prev(){
        if(this.state.page>0){
        this.setState({page:this.state.page-1})
        this.loadShops(this.state.page-1,this.state.backside)
        }
    }

    async next(){
        
        let pag=this.state.page
        let back=this.state.backside
        
        
       
        await window.ethereum.enable().then(async function (accounts) {
    
            const { REACT_APP_BOOKABI } = await process.env;
            const { REACT_APP_BOOK } = await  process.env;
            const book = new web3.eth.Contract(JSON.parse(REACT_APP_BOOKABI), REACT_APP_BOOK) 
            let res=await book.methods.door(0).call()
            let count=res[1]
        if(pag<parseInt(count/10)){
            pag++
        }
        })
        this.setState({page:pag})
        this.loadShops(pag,back)
        
        //this.render()
    }

    showNewest(){
         this.setState({backside:true})
         this.loadShops(this.state.page,true)
        }

    showOldest(){
        this.setState({backside:false})
        this.loadShops(this.state.page,false)
    }

    async loadShops(ind,back){
        document.getElementById("book_board").innerHTML=""
let pag=ind
let backside=back
        let openShop=this.openShop
       //  alert(pag)
        await window.ethereum.enable().then(async function (accounts) {
    
            const { REACT_APP_BOOKABI } = await process.env;
            const { REACT_APP_BOOK } = await  process.env;
            const book = new web3.eth.Contract(JSON.parse(REACT_APP_BOOKABI), REACT_APP_BOOK) 
            let res=await book.methods.door(0).call()
            let count=res[1]
            const { REACT_APP_METAABI } = await process.env;
            const { REACT_APP_META } = await  process.env;
            const metadata = new web3.eth.Contract(JSON.parse(REACT_APP_METAABI), REACT_APP_META) 
            
            document.getElementById("book_sublabel").innerHTML="All Shops List ("+count+")"
            let top=(pag+1)*10
            if(top>count)top=count
           // alert("count "+top)
            for(let i=0+(pag*10);i<top;i++){
              //   alert("i "+i)
                let tempI=i
                if(backside)tempI=count-i-1
               //  alert("tempI "+tempI)
                let module_address=await book.methods.door(tempI).call()
                const { REACT_APP_TICKETABI } = await  process.env;
                const door = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), module_address[0]) 
                let code= await door.methods.code().call()
                let box= await door.methods.box().call()
                const { REACT_APP_HAPPYBOXABI } = await  process.env;
                const boxC = new web3.eth.Contract(JSON.parse(REACT_APP_HAPPYBOXABI), box) 
                let master= await boxC.methods.master().call()
      
                let codeIcon=getCodeIcon(code)
                let mtdt=await metadata.methods.meta(module_address[0]).call()
                let back="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/27592dd5-bdf2-4e90-9e17-852077de3dd0/d2aytcz-fd1a408a-541d-465d-9f8d-9c662b5ba6db.jpg/v1/fill/w_900,h_600,q_75,strp/fading_blue_grunge_by_dyingbeautystock.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..JSbyhtv1aQd2O8AzG2x1UTZLfyNdtwGNtNSgzKCDFTE"
                if(mtdt.length>0){
                    console.log("MTDT",mtdt)
                    fetch(formatURL(mtdt)).then(response => response.json()).then(
                    response => {
                        console.log(JSON.stringify(response))
    
                        back=response[0]["image"]
                        //alert(back)
                        if(back=="")back='https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/27592dd5-bdf2-4e90-9e17-852077de3dd0/d2aytcz-fd1a408a-541d-465d-9f8d-9c662b5ba6db.jpg/v1/fill/w_900,h_600,q_75,strp/fading_blue_grunge_by_dyingbeautystock.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..JSbyhtv1aQd2O8AzG2x1UTZLfyNdtwGNtNSgzKCDFTE'
                        //document.getElementById("Shop_header_image").style.backgroundImage='url('+response[0]["image"]+')'
                        let container = document.createElement('div');
                        container.className = 'shop_container_minipanel';
                        let book = document.createElement('div');
                        book.className = 'book_icon';
                        book.innerHTML=""
                        let mainDiv = document.createElement('div');
                        let iDiv = document.createElement('div');
                        let backDiv = document.createElement('div');
                        let iname = document.createElement('div');
                        iDiv.id = 'shop_'+tempI;
                        iDiv.className = 'shop_link';
                        mainDiv.className = 'shop_maindiv dark darkblue';
                        backDiv.className = 'shop_backdiv';
                        iname.textContent=codeIcon+response[0]["name"];
                        iname.style.margin="100px 20px 0 0"
                        iname.style.fontSize="30px"
                        iname.style.float="right"
                        iDiv.style.backgroundImage='url('+back+')'
                        iDiv.style.backgroundSize='cover'
                        iDiv.appendChild(iname)
                        container.appendChild(mainDiv)
                        if(master.toLowerCase()==accounts[0].toLowerCase())
                        container.appendChild(book)
                        mainDiv.appendChild(backDiv)
                        mainDiv.appendChild(iDiv)
                        mainDiv.append(response[0]["description"])
                        mainDiv.addEventListener("click",  () => window.open("/smartshop/?shop="+module_address[0]))
                        document.getElementById("book_board").appendChild(container)
       
                    })
                }else{
                let container = document.createElement('div');
                container.className = 'shop_container_minipanel';
                let book = document.createElement('div');
                book.className = 'book_icon';
                book.innerHTML=""
                let iDiv = document.createElement('div');
                iDiv.id = 'shop_'+tempI;
                iDiv.className = 'shop_link_empty dark darkblue';
                let iname = document.createElement('div');
                
                iname.style.margin="27px 0 0 0"
                let iIcon = document.createElement('div');
                iIcon.style.margin="00px 0 0 10px"
                iIcon.style.float="left"
                iIcon.innerHTML=codeIcon
                iIcon.style.fontSize="50px"
                iname.innerHTML=module_address[0]
                iname.style.fontSize="20px"
                container.appendChild(iDiv)
                
                if(master.toLowerCase()==accounts[0].toLowerCase())
                container.appendChild(book)
                iDiv.appendChild(iIcon)
                iDiv.appendChild(iname)
                iDiv.style.backgroundImage='url('+back+')'
                iDiv.style.backgroundSize='cover'
                iDiv.addEventListener("click", () => window.open("/smartshop/?shop="+module_address[0]))
                document.getElementById("book_board").appendChild(container)
                }
                
            }
            })

    }
   
    

 


    render(){
        console.log(this.props.update) 
        return ( 
        <div>

        <div id="book_flyer">
            <div id="splash_mainlabel"> SmartShop Global Book</div>
            <div id="book_sublabel">All Shops List</div>
            <div id="book_control">
                <div className="book_navigation_left disabled dhover darkhover" onClick={() => this.prev()}></div>
                <div className={(!this.state.backside)?"book_navigation_left selected dark darkblue lhover lighthover":"book_navigation_left dhover darkhover"} onClick={() => this.showOldest()}></div>
                <div className={(this.state.backside)?"book_navigation_left selected dark darkblue lhover lighthover":"book_navigation_left dhover darkhover"} onClick={() => this.showNewest()}></div>
                <div className="book_navigation dhover darkhover text white" onClick={() => this.next()}>&gt;</div>
                <div className="book_navigation dhover darkhover text white" onClick={() =>  this.prev()}>&lt;</div>
                <div className="book_pagination text white">{this.state.page}</div>
                <div className="clear"></div>
                </div>
            <div id="book_board"></div>
        </div>
      
        </div>
        );
    }


}

function jumpsocial(add){
    window.open(add)
}


function fire(){}

function getCodeIcon(code){
    let t="";
    if(code=="4")t=""
    if(code=="7")t=""
    if(code=="9")t=""
    return t;
}

function formatURL(url){
    return("https://gateway.ipfs.io/ipfs/"+url);
  }


function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}



const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")



export default Book