import React, { Component } from 'react'
import Web3 from 'web3'
import './cssShopToken.css';
import utils from '../../library'
import Icons from '../Icons/Icons'
import { create, all } from 'mathjs'


const config= {
    number: 'BigNumber',                   
    precision: 64           
  }
  
  const math = create(all, config)

class ShopToken extends Component {
constructor(props){
    super()
    this.state={favorite:false,balance:0,refilling:false}
}



async buy(){
    let addr=this.props.address
    let index=this.props.index
    let ticket=this.props.ticket
    let max=await utils.ETHBalance()
    document.getElementById('cost_'+this.props.index).style.color="white"
    let spend=await web3.utils.toWei(""+(document.getElementById("cost_"+index).value*1000), 'finney')
    if(spend>=max){
        alert("If you spend all your ETH you won't have enough to pay for tyhe gas!")
    }else{
        await window.ethereum.enable().then(async function (accounts) { 
            const { REACT_APP_TICKETABI } = process.env;
            const { REACT_APP_TICKET } = process.env;
            const door = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), ticket) 
            await door.methods.buy(addr,"0x54d5aC00babbF6884336D313d7Afe08279015DD4").send({from: accounts[0] , gasLimit: 223456 , value: web3.utils.toWei(""+(document.getElementById("cost_"+index).value*1000), 'finney')})
        })  
        this.loadToken()
    }
}

async setPrice(){
    let addr=this.props.address
    let index=this.props.index
    let priceList=this.props.priceList
    if(document.getElementById("ShopToken_item_setprice_field_"+index).value*1000>=1){
        await window.ethereum.enable().then(async function (accounts) { 
            const { REACT_APP_PriceListABI } = process.env;
            const prices = new web3.eth.Contract(JSON.parse(REACT_APP_PriceListABI), priceList) 
            await prices.methods.setPrice(addr,document.getElementById("ShopToken_item_setprice_field_"+index).value*1000).send({from: accounts[0] , gas: 123456 })
            document.getElementById('Shop_item_price_'+index).innerHTML=document.getElementById("ShopToken_item_setprice_field_"+index).value
        }) 
        this.loadToken() 
    }else{
        alert("PRICE MUST BE >= 0.001")
    }
}

async calculate_price(){
    let amount=math.bignumber(document.getElementById('Shop_item_setamount_field_'+this.props.index).value)
    if(amount>this.state.balance){
        document.getElementById('Shop_item_setamount_field_'+this.props.index).value=this.state.balance
        amount=math.bignumber(this.state.balance)
    }
    //let price=parseFloat(1000000*amount)*(parseFloat(this.props.price)/1000)/1000000
    let a=math.bignumber((1000000*amount)*(this.props.price/1000))
    alert(a)
    let b=math.bignumber(1000000)
    alert(b)
    alert(math.divide(a,b))
    let price=math.divide(a,b)
    document.getElementById("cost_"+this.props.index).style="text-align:right;"
    document.getElementById('cost_'+this.props.index).value=price
    let max=await utils.ETHBalance()
    document.getElementById('cost_'+this.props.index).style.color="white"
    document.getElementById("Shop_item_button_"+this.props.index).style.color="white"
    if((price>>math.bignumber(max))||(price==0)){
        alert(max)
        document.getElementById('cost_'+this.props.index).style.color="red"
        document.getElementById("Shop_item_button_"+this.props.index).style.color="red"
    }
}

calculate_backprice(){
    let cost=document.getElementById('cost_'+this.props.index).value
    let amount=parseFloat(1000000*cost)/(parseFloat(this.props.price)/1000)/1000000
    document.getElementById('Shop_item_setamount_field_'+this.props.index).value=amount
    document.getElementById('Shop_item_setamount_field_'+this.props.index).style.color="white"
    document.getElementById("Shop_item_button_"+this.props.index).style.color="white"
    if(this.state.balance<amount){
        document.getElementById('Shop_item_setamount_field_'+this.props.index).style.color="red"
        document.getElementById("Shop_item_button_"+this.props.index).style.color="red"
    }
}

setMax(){
    document.getElementById('Shop_item_setamount_field_'+this.props.index).style.color="white"
    document.getElementById('Shop_item_setamount_field_'+this.props.index).value=this.state.balance
    this.calculate_price()
}

async setMin(){
    let decimals = await utils.decimals(this.props.address)
    document.getElementById('Shop_item_setamount_field_'+this.props.index).style.color="white"
    document.getElementById('Shop_item_setamount_field_'+this.props.index).value=math.divide(math.bignumber(1.0),math.bignumber(Math.pow(10,decimals)))
    this.calculate_price()
}

async setETHMax(){
    let max=await utils.ETHBalance()
    document.getElementById('cost_'+this.props.index).style.color="white"
    document.getElementById('cost_'+this.props.index).value=max
    this.calculate_backprice()
}

async refillMode(){
    if(!this.state.refilling){
    this.setState({refilling:true})
    this.setMaxSendToBox()
    }else{
    this.setState({refilling:false})
    }
}

check_price(){
    document.getElementById("ShopToken_item_setprice_button_"+this.props.index).style.color="white"
    if(this.state.refilling){
        if(document.getElementById("ShopToken_item_setprice_field_"+this.props.index).value>document.getElementById("ShopToken_item_setprice_field_"+this.props.index).getAttribute("max_balance"))  
        document.getElementById("ShopToken_item_setprice_button_"+this.props.index).style.color="red"
        document.getElementById("ShopToken_item_setprice_field_"+this.props.index).style.color="red"
    }
    if(document.getElementById("ShopToken_item_setprice_field_"+this.props.index).value=="")
    document.getElementById("ShopToken_item_setprice_button_"+this.props.index).style.color="red"
    if(parseFloat(document.getElementById("ShopToken_item_setprice_field_"+this.props.index).value)<0.001){
        document.getElementById("ShopToken_item_setprice_button_"+this.props.index).style.color="red"
        if(parseFloat(document.getElementById("ShopToken_item_setprice_field_"+this.props.index).value)>0){
            document.getElementById("ShopToken_item_setprice_field_"+this.props.index).value="0.001"
            document.getElementById("ShopToken_item_setprice_button_"+this.props.index).style.color="white"
        }
    }
}

async setMaxSendToBox(){
    let addr=this.props.address
    let index=this.props.index
    
    await window.ethereum.enable().then(async function (accounts) { 
        let bal=await utils.balanceOf(accounts[0],addr)
        document.getElementById("ShopToken_item_setprice_field_"+index).setAttribute("max_balance",bal )   
        document.getElementById("ShopToken_item_setprice_field_"+index).value=bal
        document.getElementById("ShopToken_item_setprice_field_"+index).style.color="white"
        document.getElementById("ShopToken_item_setprice_button_"+index).style.color="white"
        if(bal==0){
            document.getElementById("ShopToken_item_setprice_field_"+index).style.color="red"
            document.getElementById("ShopToken_item_setprice_button_"+index).style.color="red"
        }
    })
}

async sendToBox(){
    let addr=this.props.address
    let index=this.props.index
    let box=this.props.db
    if(document.getElementById("ShopToken_item_setprice_field_"+index).value>0){
        await window.ethereum.enable().then(async function (accounts) { 
            const { REACT_APP_ABI } = process.env;
            const tkn = new web3.eth.Contract(JSON.parse(REACT_APP_ABI), addr) 
            await tkn.methods.transfer(box,web3.utils.toWei(""+document.getElementById("ShopToken_item_setprice_field_"+index).value)).send({from: accounts[0] , gasLimit: 149174 })
           }) 
        this.setMaxSendToBox()() 
    }else{
        alert("SEND MORE THAN 0")
    }
}

getIcon(icon,dim){
    return <Icons icon={icon} d={dim}/>
}

async loadToken(){
    let addr=this.props.address
    let index=this.props.index
    let ticket=this.props.ticket
    let db=this.props.db
    let bal
    if(!this.props.ghost){
    await utils.checkFavorite(addr,index)
    await utils.checkGhost(addr,index)
    }else{
    await utils.checkGhostFavorite(addr,index)
    }
    await window.ethereum.enable().then(async function (accounts) { 
        try{ 
            let name = await utils.nameOf(addr)
            let symbol = await utils.symbolOf(addr)
            let box = await utils.ticket2Box(ticket)  
            bal = await utils.balanceOf(box,addr)
            let decimals = await utils.decimals(addr)
            document.getElementById("Shop_item_"+index).textContent=name   
            document.getElementById("Shop_item_setamount_field_"+index).value=bal
            document.getElementById("Shop_item_setamount_field_"+index).setAttribute("box_balance",bal )   
            document.getElementById("cost_"+index).style="text-align:left;"
            document.getElementById("cost_"+index).value=symbol
        }catch(e){console.log("SHOP TOKEN ERROR "+db+e)}
    })
this.setState({balance:bal})
this.render()
}

    componentDidMount(){
        this.loadToken()
    }

    setCookie(){
        utils.setCookie("favorite",this.props.address,this.props.index)
        this.loadToken()
    }

    render(props){
    console.log("SHOP TOKEN PROPS>",this.props)
        return ( 
    <div className="ShopToken_row_container">
        <div className="ShopToken_product_tool">
            <div id={"favorite_"+this.props.index} className={(this.state.favorite)?"favorite":"notfavorite"} onClick={() => this.setCookie()}><Icons icon="star" d="50"/></div>
            <div id={"favorite_"+this.props.index} className={(this.state.favorite)?"favorite2 dhover darkhover":"notfavorite2 dhover darkhover"} onClick={() => openProduct(this.props.ticket,this.props.index)}><Icons icon="pill" d="50"/></div>
        </div>
        <div className="ShopToken_row">        
            <div id={"Shop_Token_item_panel_"+this.props.index} className={(this.state.balance==0)?"Shop_Token_item_panel_disabled dark darkblue":"Shop_Token_item_panel dark darkblue"}>
                {(this.props.edit)?<div className={(this.state.refilling)?"ShopToken_refill_true light lightblue":"ShopToken_refill_false"} onClick={() => this.refillMode()}><Icons icon="drops" d="50"></Icons></div>:""}
               <img className="ShopToken_item_icon" onClick={() => window.open('https://ropsten.etherscan.io/address/'+this.props.address)} src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
               <div id={"Shop_item_"+this.props.index} className="ShopToken_item_label text white">loading...</div>
               <img className="ShopToken_item_price_icon" src="https://i.imgur.com/FPL7c5a.png"></img>
               <span id={"Shop_item_price_"+this.props.index} className="ShopToken_item_price text white right">{(this.props.price)/1000}</span>
               <div className="clear"></div>
                {(this.props.edit)?
                <div className="ShopToken_item_setprice light lightblue text white">
                    <input id={"ShopToken_item_setprice_field_"+this.props.index} className="ShopToken_item_setprice_field light lightblue text white" type="text"onKeyUp={() => this.check_price()} onKeyPress={() => this.check_price()}></input>
                    {(!this.state.refilling)
                    ?<div id={"ShopToken_item_setprice_button_"+this.props.index} className="ShopToken_item_setprice_button dark darkblue text white" onClick={() => this.setPrice()}><Icons icon="pen" d="40"/> <div className="button_label_right">Set Price</div></div>
                    :<div id={"ShopToken_item_setprice_button_"+this.props.index} className="ShopToken_item_setprice_button dark darkblue text white"  onClick={() => this.sendToBox()}><div className="button_label_left">Send to </div><Icons icon="box" d="40"/></div>
                    }
                </div>
                :
               <div className="ShopToken_item_setprice light lightblue text white">
                   <div className="ShopToken_buydrop dhover darkhover" onClick={() => this.setMin()}><Icons icon="drop" d="50"/> </div>
                   <input id={"Shop_item_setamount_field_"+this.props.index} className="ShopToken_item_setamount_field light lightblue text white" type="text" onKeyUp={() => this.calculate_price()} onKeyPress={() => this.calculate_price()}></input>
                   <img className="ShopToken_item_icon"  onClick={() => this.setMax()} src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
                   <input  id={"cost_"+this.props.index} className="ShopToken_item_setamount_field light lightblue text white" type="text"  onKeyUp={() => this.calculate_backprice()} onKeyPress={() => this.calculate_backprice()}></input>
                   <img className="ShopToken_item_icon"  onClick={() => this.setETHMax()} src="https://i.imgur.com/FPL7c5a.png"></img>
                   <div id={"Shop_item_button_"+this.props.index} className="ShopToken_item_setprice_button dark darkblue text white" onClick={() => this.buy()}><Icons icon="chart" d="40"/><div className="button_label_right">buy!</div></div>
               </div>
                }          
            </div>
        </div>
    </div>   
        );
    }


}

function openProduct(ticket,index){ 
window.open("/smartshop/?shop="+ticket+"&index="+index.substr(index.lastIndexOf("x")+1,10))
}
 
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default ShopToken