import React, { Component } from 'react'
import Lipshead from '../Lipshead/Lipshead';
import Pad from '../Pad/Pad';
import './css.css';
import utils from './../../library'


class Home extends Component { 
    constructor(props) {
        super(props)
        this.state = { panel: 'Splash' , token : '0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD' , micro : "ProposalSoftInflator"}
        if(utils.getUrlVars()["account"])this.state.panel="Beanometer"
    }

    update(panelx,tokenx){
        this.setState({panel:panelx,token:tokenx})
        window.history.pushState('new_panel', panelx, '/');
        console.log("changed page!",panelx);
     }

     jumptoshop(){
        this.setState({panel:'default'})

        console.log("changed page!","shop");
     }

     micro(micr){
        this.setState({micro:micr})
        console.log("changed micro!");
     }
   

    render(){ 
        
        
       let panel=this.state.panel
       if(utils.getUrlVars()["shop"])panel="Prices"
       if(utils.getUrlVars()["account"])panel="Avatar"
       if(utils.getUrlVars()["ghost"])panel="Ghost"
        return (
        <div id="home" className="dark darkblue">
           <Lipshead update={this.update.bind(this)}></Lipshead>
           <Pad panel={panel} update={this.update.bind(this)} jumptoshop={this.jumptoshop.bind(this)} micro={this.micro.bind(this)}/>
        </div>);
    }
}



export default Home