import React, { Component } from 'react'
import Web3 from 'web3'
import Box1_0 from '../Box1_0/Box1_0';
import Box1_0Installer from '../Box1_0/Box1_0Installer';

import './css.css';



class BoxesInstallerManager extends Component {
constructor(props){
    super()
    this.loadModules = this.loadModules.bind(this)
this.state={enabled_items:[],disabled_items:[]}
this.renderAgain = this.renderAgain.bind(this);
}

async getCode(address){
    console.log("getCode"+address)
    let t=0
try{
    const { REACT_APP_TICKETABI } = process.env;
let module = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), address)
let codex=await module.methods.code().call()
t=codex
}catch(f){}
return t
}

renderAgain(){
 
    //this.loadModules()
    this.render()
}

    componentDidMount(){
     // this.loadModules()
    }


   async loadModules(){
    let total_modules=[]
    let total_disabled_modules=[]
    let db=this.props.db
    let rendAgain=this.renderAgain
    await window.ethereum.enable().then(async function (accounts) {   
        const { REACT_APP_HAPPYBOXABI } = process.env;

        const happybox = new web3.eth.Contract(JSON.parse(REACT_APP_HAPPYBOXABI), db) 
        
        let count=0;
        for(let i=0;i<count+1;i++){
            let addr
            let found=false
            try{
                addr=await happybox.methods.modules_list(i).call()
            console.log("MODULE ADDRESS: "+addr)
                found=true
                count++
            }catch(e){}  
            if(found){
                
                
                let code=0
                
                
                try{
                    const { REACT_APP_TICKETABI } = process.env;
                let module = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), addr)
                let codex=await module.methods.code().call()
                code=codex
                }catch(f){}
                

                let enabled=await happybox.methods.modules(addr).call()
                if(enabled){
                    console.log("yes")
                    let t
                if(code==0)t=<Box1_0 address={addr} key={i} enabled={true} box={db} reset={rendAgain}></Box1_0>
                if(code==1)t=<Box1_0 address={addr} key={i} enabled={true} box={db} reset={rendAgain}></Box1_0>
                if(code==2)t=<Box1_0 address={addr} key={i} enabled={true} box={db} reset={rendAgain}></Box1_0> 
                    total_modules.push(t)
                }else{
                    console.log("no")
                    let t
                    if(code==0)t=<Box1_0 address={addr} key={i} enabled={false} box={db} reset={rendAgain}></Box1_0>
                    if(code==1)t=<Box1_0 address={addr} key={i} enabled={false} box={db} reset={rendAgain}></Box1_0>
                    if(code==2)t=<Box1_0 address={addr} key={i} enabled={false} box={db} reset={rendAgain}></Box1_0>
                    total_disabled_modules.push(t)
                }
              
            }
            
        console.log(`modules: ${count}`)
        }
    
    })
    this.setState({enabled_items : total_modules})
    this.setState({disabled_items : total_disabled_modules})
    this.render()
}

    render(props){
    console.log(this.state.enabled_items)

    let addr
            let db
     
            let i
        return ( 
            
        <div className="BoxesInstaller_Modules_Panel dark darkblue">
            <img className="Boxes_add_box_icon" src="https://i.imgur.com/YKhumij.png"></img>
            <div className="clear"></div> 
            <div className="BoxesInstaller_Modules_Container light lightblue">  
            
            <Box1_0Installer address={addr} key={i} enabled={false} box={db} reset={this.props.reset}></Box1_0Installer>
              
            </div>
        </div>
            
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}


        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default BoxesInstallerManager