    async loadHeader(){
            let addr=""
            let shopNam=""
            let shopDescriptio=""
            await window.ethereum.enable().then(async function (accounts) {
                let metafound=false
                let mtdt
                if(utils.getUrlVars()["shop"]){
                    addr=utils.getUrlVars()["shop"]
                    mtdt = await utils.metadata(addr,accounts[0])
                    if(mtdt.length>=0)metafound=true
                }
                if((!utils.getUrlVars()["shop"])||(!metafound)){
                    addr=accounts[0]
                    mtdt=await utils.metadata(addr,addr)
                }
                document.getElementById("Shop_twitter_pan").style.display='none'
                document.getElementById("Shop_discord_pan").style.display='none'       
                if(mtdt.length>0){
                    console.log("MTDT",mtdt)
                    await fetch(utils.formatURL(mtdt)).then(response => response.json()).then(
                    response => {
                        console.log(JSON.stringify(response))
                        shopNam=response[0]["name"]  
                        shopDescriptio=response[0]["description"]
                        document.getElementById("Ghost_header_mainlabel").textContent=shopNam
                        document.getElementById("Shop_header_description").textContent=shopDescriptio
                        let back=response[0]["image"]
                        if(back=="")back='https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/27592dd5-bdf2-4e90-9e17-852077de3dd0/d2aytcz-fd1a408a-541d-465d-9f8d-9c662b5ba6db.jpg/v1/fill/w_900,h_600,q_75,strp/fading_blue_grunge_by_dyingbeautystock.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..JSbyhtv1aQd2O8AzG2x1UTZLfyNdtwGNtNSgzKCDFTE'
                        document.getElementById("Shop_header_image").style.backgroundImage='url('+back+')'           
                        if(response[0]["twitter"].length>0){
                            document.getElementById("Shop_twitter_pan").addEventListener("click", () => utils.jumpsocial("https://twitter.com/"+response[0]["twitter"].replace(/[^\w\s]/gi, '')))
                            document.getElementById("Shop_twitter_pan").style.display='visible'                     
                        }
                        if(response[0]["discord"].length>0){
                            document.getElementById("Shop_discord_pan").addEventListener("click",  () => utils.jumpsocial("https://discord.gg/"+response[0]["discord"].replace(/[^\w\s]/gi, '')))
                            document.getElementById("Shop_discord_pan").style.display='visible'
                        }       
                    })
                }
            })
            await this.setState({shopName:shopNam,shopDescription:shopDescriptio})                   
        }

        setCookie(cname, cvalue,id) {
            let s=this.getCookie(cname)
            let arr=s.split(",")
            let arr2=[]
            let t=false
            for(let i=0;i<arr.length;i++){               
                if(arr[i]==cvalue+"_"+id){
                    t=true 
                    document.getElementById("Shop_settings_favoriteShop").style.opacity=0.3              
                }else{
                    arr2.push(arr[i])
                    document.getElementById("Shop_settings_favoriteShop").style.opacity=1
                }
            } 
            if(!t){
                arr2.push(cvalue+"_"+id)   
            }
            document.cookie = cname + "=" + arr2.toString() + "; path=/"        
        }
        
        getCookie(cname) {
            var name = cname + "=";
            var decodedCookie = decodeURIComponent(document.cookie);
            var ca = decodedCookie.split(';');
            for(var i = 0; i <ca.length; i++) {
              var c = ca[i];
              while (c.charAt(0) == ' ') {
                c = c.substring(1);
              }
              if (c.indexOf(name) == 0) {
                return c.substring(name.length, c.length);
              }
            }
            return "";
          }
    

