import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';



class ShopPair extends Component {
constructor(props){
    super()
  this.state={favorite:false}

}

setCookie(cname, cvalue,id) {

    let s=this.getCookie(cname)
    let arr=s.split(",")
    let arr2=[]
    let t=false
    for(let i=0;i<arr.length;i++){
        //if(arr[i].substr(arr[i].indexOf("_")+1)==id){
            if(arr[i]==cvalue+"_"+id){
            t=true
        }else{
            arr2.push(arr[i])
        }
    }
  
    if(!t){
        arr2.push(cvalue+"_"+id)
      
    }
    document.cookie = cname + "=" + arr2.toString() + ";"
    
 
    this.loadToken()
}

getCookie(cname) {
    var name = cname + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(';');
    for(var i = 0; i <ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) == ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) == 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

async buy(){
        let addr=this.props.address
        let index=this.props.index
        window.ethereum.enable().then(async function (accounts) { 
            const { REACT_APP_TICKETABI } = process.env;
            const { REACT_APP_TICKET } = process.env;
            const door = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), REACT_APP_TICKET) 
            door.methods.buy(addr,accounts[0]).send({from: accounts[0] , gas: 123456 , value: document.getElementById("cost_"+index).value})
    })  
}

async setPrice(){
    let addr=this.props.address
    let index=this.props.index
    let priceList=this.props.priceList
    window.ethereum.enable().then(async function (accounts) { 
        const { REACT_APP_PriceListABI } = process.env;
        
        const prices = new web3.eth.Contract(JSON.parse(REACT_APP_PriceListABI), priceList) 
        await prices.methods.setPrice(addr,document.getElementById("Shop_item_setprice_field_"+index).value*1000).send({from: accounts[0] , gas: 123456 })
        document.getElementById('Shop_item_price_'+index).innerHTML=document.getElementById("Shop_item_setprice_field_"+index).value
    })  
}

calculate_price(){
    let price=parseInt(document.getElementById('Shop_item_setamount_field_'+this.props.index).value)*parseInt(this.props.price)/1000
    document.getElementById("cost_"+this.props.index).style="text-align:right;"
    document.getElementById('cost_'+this.props.index).value=price
}

async loadToken(){
    let addr=this.props.address
    let index=this.props.index
    let s=this.getCookie("favorite")
    let arr=s.split(",")
    let t=false
    //alert(arr)
    document.getElementById("favorite_"+index).style.opacity=0.3
    for(let i=0;i<arr.length;i++){
     //if("_"+arr[i].substr(0,arr[i].indexOf("_"))=="_"+addr)
     if(arr[i]==addr+"_"+index)
    document.getElementById("favorite_"+index).style.opacity=1
    }
    //
    await window.ethereum.enable().then(async function (accounts) { 
        try{ 
            const { REACT_APP_ABI } = process.env; 
        const tkn = new web3.eth.Contract(JSON.parse(REACT_APP_ABI), addr) 
        let name = await tkn.methods.name().call()
        document.getElementById("ShopPair_item_"+index).innerHTML=name
        let dec = await tkn.methods.decimals().call()
        let symbol = await tkn.methods.symbol().call()
        const { REACT_APP_GIFTER } = process.env;
    
        let bal = await tkn.methods.balanceOf(REACT_APP_GIFTER).call()/Math.pow(10,dec)
        document.getElementById("Shop_item_setamount_field_"+index).value=bal
        document.getElementById("cost_"+index).style="text-align:left;"
        document.getElementById("cost_"+index).value=symbol

    
        }catch(e){}
})

this.render()
}

    componentDidMount(){
        this.loadToken()
      
    }

    render(props){
    
        return ( 
            <div className="row_container">
            <div id={"favorite_"+this.props.index} className={(this.state.favorite)?"favorite":"notfavorite"} onClick={() => this.setCookie("favorite",this.props.address,this.props.index)}></div>
        <div className="Shop_row">        
            <div className="Shop_item_panel">
               <img className="Shop_item_icon" onClick={() => window.open('https://etherscan.io/address/'+this.props.memory)} src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
               <div id={"ShopPair_item_"+this.props.index} className="Shop_item_label">loading...</div>
               <img className="Shop_item_price_icon" src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
               <span id={"Shop_item_price_"+this.props.index} className="Shop_item_price right">{(this.props.price)/1000}</span>
               <div className="clear"></div>
                {(this.props.edit)?
                <div className="Shop_item_setprice">
                    <input id={"Shop_item_setprice_field_"+this.props.index} className="Shop_item_setprice_field" type="text"></input>
                    <input className="Shop_item_setprice_button" type="button" value=" Set Price" onClick={() => this.setPrice()}></input>
                </div>
                :
               <div className="Shop_item_setprice">
                   <input id={"Shop_item_setamount_field_"+this.props.index} className="Shop_item_setamount_field" type="text" onKeyUp={() => this.calculate_price()} onKeyPress={() => this.calculate_price()}></input>
                   <img className="Shop_item_icon"  src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
                   <input  id={"cost_"+this.props.index} className="Shop_item_setamount_field" type="text"  onKeyUp={() => this.calculate_price()} onKeyPress={() => this.calculate_price()}></input>
                   <img className="Shop_item_icon"  src="https://dapp.dfohub.com/assets/img/default-logo.png"></img>
                   <input className="Shop_item_setprice_button" type="button" value=" buy!" onClick={() => this.buy()}></input>
               </div>
    }
                
               </div>
        </div>
        </div>  
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}


        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default ShopPair