import React, { Component } from 'react'
import Web3 from 'web3'
import Ticket1_0 from '../Ticket1_0/Ticket1_0';
import Freebies1_0 from '../Freebies1_0/Freebies1_0';
import PairSwapper1_0 from '../PairSwapper1_0/PairSwapper1_0';
import InstallerManager from '../InstallerManager/InstallerManager';
import './css.css';



class ShopModulesPanel extends Component {
constructor(props){
    super()
    this.loadModules = this.loadModules.bind(this)
this.state={focus:"",enabled_items:[],disabled_items:[]}
this.renderAgain = this.renderAgain.bind(this);
}

async getCode(address){
    console.log("getCode"+address)
    let t=0
try{
    const { REACT_APP_TICKETABI } = process.env;

let module = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), address)
let codex=await module.methods.code().call()
t=codex
}catch(f){}
return t
}

renderAgain(){

    this.loadModules()
    this.render()
}

    componentDidMount(){
        if(this.props.db!="")
      this.loadModules()
    }


   async loadModules(){
    let total_modules=[]
    let total_disabled_modules=[]
    let db=this.props.db
    let rendAgain=this.renderAgain
    let ticket=this.props.ticket
    let freebie=this.props.freebie
    let swapper=this.props.swapper
    await window.ethereum.enable().then(async function (accounts) {  
        const { REACT_APP_HAPPYBOXABI } = process.env; 
        console.log("DB",db)
        const happybox = new web3.eth.Contract(JSON.parse(REACT_APP_HAPPYBOXABI), db) 
        
        let count=0;
        for(let i=0;i<count+1;i++){
            let addr
            let found=false
            try{
            addr=await happybox.methods.modules_list(i).call()
            console.log("MODULE ADDRESS: "+addr)
                found=true
                count++
            }catch(e){console.log("ShopModules loadModules()",e)}  
            if(found){
                
                
                let code=0
                
                
                try{
                    const { REACT_APP_TICKETABI } = process.env; 
                let module = new web3.eth.Contract(JSON.parse(REACT_APP_TICKETABI), addr)
                let codex=await module.methods.code().call()
                code=codex
                }catch(f){console.log("ShopModules codex",f)}
                

                let enabled=await happybox.methods.modules(addr).call()
                let t

                if(code==4)t=<Ticket1_0 address={addr} key={i} index={i} enabled={enabled} box={db} reset={rendAgain} ticket={ticket}></Ticket1_0>
                if(code==7)t=<Freebies1_0 address={addr} key={i} index={i} enabled={enabled} box={db} reset={rendAgain} freebie={freebie}></Freebies1_0>
                if(code==9)t=<PairSwapper1_0 address={addr} key={i} index={i} enabled={enabled} box={db} reset={rendAgain} swapper={swapper}></PairSwapper1_0>
                
                if(enabled){
                    total_modules.push(t)
                }else{
                    total_disabled_modules.push(t)
                }
              
            }
            
        console.log(`modules: ${count}`)
        }
    
    })
    this.setState({focus:db})
    this.setState({enabled_items : total_modules})
    this.setState({disabled_items : total_disabled_modules})
    this.render()
}

    render(props){
    console.log("ENABLED ITEMS",this.state.enabled_items)
    console.log("DISABLED ITEMS",this.state.disabled_items)
    if(this.props.db!=this.state.focus)this.loadModules()
    
        return ( <>
            {(this.props.db!="")?
        <div className="Shop_Modules_Panel dark darkblue text white">
            <div className="Shop_Modules_index" onClick={() => window.open("https://ropsten.etherscan.io/address/"+this.props.db)}> #{this.props.boxindex}</div>
            <div className="Shop_Modules_mainlabel text white"> modules  </div> 
            <div className="clear"></div> 
            <div className="Shop_Modules_Container light lightblue">  
                {
                  this.state.enabled_items  
                } 
                {
                  this.state.disabled_items  
                }
            <InstallerManager db={this.props.db} reset={this.renderAgain}></InstallerManager>
              
            </div>
        </div>
        :<div></div>
            }
        </>
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default ShopModulesPanel