import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';




class Token extends Component {
    componentDidMount() {
        this.loadBlockchainData(this.props.token)
    }
    
    
        async loadBlockchainData(prp) {
            window.ethereum.enable().then(async function (accounts) {  
                    let token=prp;
                    if(getUrlVars()["token"])token=getUrlVars()["token"]
                    const beans = new web3.eth.Contract(ABI, token) 
                    let decimalz = await beans.methods.decimals().call()
                    //let decimalz = 18
                    let supply = await beans.methods.totalSupply().call()/Math.pow(10, decimalz)   
                    let symbol = await beans.methods.symbol().call()                                        
                    document.getElementById("Token_supply").innerHTML=supply+" "+symbol;
                    let name = await beans.methods.name().call()                               
                    document.getElementById("Token_name").innerHTML=name;
            })
        }
    render(){
        console.log(this.props.update) 
        return ( 
        <div id="Token_flyer">
            <div id="Token_mainlabel">Token</div>

            <div className="Token_image_container"><img id="Token_image"  alt="BEANS"  src="https://dapp.dfohub.com/assets/img/default-logo.png"/></div> 
            <div id="Token_label">Type: ERC20</div>
            <div id="Token_name"></div>
            <div id="Token_info">
            <div id="Token_supply">supply:</div>

            </div>
        </div>
        );
    }

    //<div className="Token_image_container"><input  id="Token_button" type="button" value="ETHERSCAN" onClick={() => this.props.update("HappyFarm")}/></div>
    //<img id="Token_image"  alt="BEANS"  src="https://dapp.dfohub.com/assets/img/default-logo.png"/>


}

