import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';



class TicketInstaller1_0 extends Component {
constructor(props){
    super()


}

async installNow(){
    
    let addr=this.props.address
    let db=this.props.box
    await window.ethereum.enable().then(async function (accounts) {   
        const { REACT_APP_BOX1_0FACTORYABI } = process.env;
        const { REACT_APP_BOX1_0FACTORY } = process.env;
        const factory = new web3.eth.Contract(JSON.parse(REACT_APP_BOX1_0FACTORYABI), REACT_APP_BOX1_0FACTORY) 
        await factory.methods.install().send({gas: 1600000, from: accounts[0]})
        //reload 
    })
    this.props.reset()
}


    render(props){
    console.log(props)
   
            return ( <div>
                <div className="Shop_Modules_Installer dark darkblue text white">                        
                                <span className="Shop_module_icon"> </span> HappyBox 1.0 installer
                                <div className="Box_desc text white">shop basic storage</div>
                                <input className="Shop_module_install_button light lightblue" onClick={() => this.installNow()} type="button" value=" install"></input>              
                            </div>
                        </div>         
                );   
        
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}



        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default TicketInstaller1_0