import React, { Component } from 'react'
import Web3 from 'web3'
import './cssTicketInstaller.css';
import Icons from '../Icons/Icons'

class TicketInstaller1_0 extends Component {
constructor(props){
    super()
}

async installNow(){
    let addr=this.props.address
    let db=this.props.box
    await window.ethereum.enable().then(async function (accounts) {   
        const { REACT_APP_TICKET1_0FACTORYABI } = process.env;
        const { REACT_APP_TICKET1_0FACTORY } = process.env;
        const factory = new web3.eth.Contract(JSON.parse(REACT_APP_TICKET1_0FACTORYABI), REACT_APP_TICKET1_0FACTORY) 
        await factory.methods.install(db).send({gas: 1000000, from: accounts[0]})
    })
    this.props.reset()
}

async enableNow(){
    let addr=this.props.address
    let db=this.props.box
    await window.ethereum.enable().then(async function (accounts) {   
        const { REACT_APP_HAPPYBOXABI } = process.env;
        const { REACT_APP_TICKET1_0FACTORY } = process.env;
        const happybox = new web3.eth.Contract(JSON.parse(REACT_APP_HAPPYBOXABI), db) 
        await happybox.methods.set(REACT_APP_TICKET1_0FACTORY,true,1).send({gas: 123456, from: accounts[0]})
    })
    this.props.enable("ticket1_0installer")
}

    render(props){
            console.log(props)
            return ( 
            <div>         
                <div className={(this.props.enabled)? "TicketInstaller_Modules_Installer dark darkblue text white" : "TicketInstaller_Modules_Installer_disabled dark darkblue text white" }>     
                    <span className="TicketInstaller_module_icon"><Icons icon="ticket" d="60"/> </span> ticket 1.0 installer
                        <br></br> 
                        <div className="TicketInstaller_desc text white">accepts payment in ETH</div>
                        {(this.props.enabled)?
                            <div className="TicketInstaller_module_install_button" onClick={() => this.installNow()}><Icons icon="install" d="40"/><div className="button_label_right text white"> install</div></div>              
                        :
                            <div className="TicketInstaller_module_install_button" onClick={() => this.enableNow()}><Icons icon="thunder" d="40"/><div className="button_label_right text white"> enable</div></div>              
                        }
                </div>
            </div>         
        );     
    }
}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default TicketInstaller1_0