import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';
import utils from './../../library'



class SingleShopMenu extends Component {
constructor(props){
    super()
}

componentDidMount(){
    utils.loadShopColors(utils.getUrlVars()["shop"])
    }

    render(props){
    console.log(props)

    
        return ( 
        <div className="Shop_row">        
            <div className="SingleShop_menu_panel dark darkblue">
            <span className="SingleShop_menu_item lhoverx lighthover" onClick={() => this.props.setMode("default")}></span>
            <span className="SingleShop_menu_item lhover lighthover" onClick={() => this.props.setMode("meta")}></span>
            <span className="SingleShop_menu_item lhover lighthover" onClick={() => this.props.setMode("instructions")}></span>
               <span className="SingleShop_menu_item lhover lighthover right" onClick={() => this.props.update("Book")}></span><span className="SingleShop_menu_item  lhover lighthover right"></span>
               <div className="clear"></div>
                     
            </div>
        </div>
            
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}


        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default SingleShopMenu